import React, { Component } from 'react';
import './../../Styles/Styles.scss';
import './GhiNhoContent.scss'
import Aux from '../../../hoc/_Aux/_Aux';
import instanceAxios from '../../../axios-orders';

class GhiNhoContent extends Component {
  state = {
    mon: '',
    lop: '',
    chuong: '',
    bai: '',
    chuyende: false,
    congthuc: 'Cng thc'
  }

  onChangeHandler = (event) => {
    this.setState({
      ...this.state,
      [event.target.name]: event.target.value
    })
  }

  switchChuyenDeHandler = (event) => {
    event.preventDefault();
    event.stopPropagation();
    console.log(event.target)
    this.setState({
      ...this.state,
      chuyende: !this.state.chuyende
    })
  }

  switchCongThucHandler = (event) => {
    event.preventDefault();
    // let ghinho = document.getElementById('ghinho');
    // let btns = ghinho.querySelectorAll('.btn');
    // console.log(btns)
    let text = this.state.congthuc === 'Cng thc' ? 'Phng php' : 'Cng thc';
    this.setState({
      ...this.state,
      congthuc: text
    })
  }

  onSubmitHandler = (event) => {
    event.preventDefault();
    this.props.dataHandler();

    instanceAxios.get('/api/method').then(res => {
      console.log(res.data.data)
    })
  }

  render() {
    let congthucStyle = this.state.congthuc === 'Cng thc'? "btn-warning": null;

    let chuyende = <Aux>
      <div className="col-6">
        <div className="input-group input-group-sm">
          <div className="input-group-prepend">
            <button className={[congthucStyle, "btn btn-switch"].join(' ')} onClick={this.switchChuyenDeHandler}><i className="fa fa-refresh"></i> Lp</button>
          </div>
          <select name="lop" id="lop" className="form-control">
            <option value="Lp 10">Lp 10</option>
            <option value="Lp 11">Lp 11</option>
            <option value="Lp 12">Lp 12</option>
          </select>
        </div>
      </div>

      <div className="col-6">
        <div className="input-group input-group-sm">
          <div className="input-group-prepend">
            <button className={[congthucStyle, "btn"].join(' ')} disabled>Chng</button>
          </div>
          <select name="chuong" id="chuong" className="form-control">
            <option value="Chng 1">Chng 1</option>
            <option value="Chng 2">Chng 2</option>
            <option value="Chng 3">Chng 3</option>
          </select>
        </div>
      </div>

      <div className="col-6">
        <div className="input-group input-group-sm">
          <div className="input-group-prepend">
            <button type="button" className={[congthucStyle, "btn"].join(' ')} disabled>bi</button>
          </div>
          <select name="bai" id="bai" className="form-control">
            <option value="Bi 1">Bi 1</option>
            <option value="Bi 2">Bi 2</option>
            <option value="Bi 3">Bi 3</option>
          </select>
        </div>
      </div>
    </Aux>

    if (this.state.chuyende === true) {
      chuyende = <Aux>
        <div className="col-6">
          <div className="input-group input-group-sm">
              <button className={[congthucStyle, "btn btn-sm btn-switch w-100"].join(' ')} onClick={this.switchChuyenDeHandler}><i className="fa fa-refresh"></i> Chuyn </button>
          </div>
        </div>
      </Aux>
    }

    return (
      <form className="Form" onSubmit={this.onSubmitHandler}>
        <div className="row">
          <div className="col-12">
            <div className="input-group input-group-sm mb-2">
              <input type="text" className="form-control" placeholder="Tm kim ..."/>
              <div className="input-group-append">
                <button className="btn btn-danger" type="button">Tm</button>
              </div>
            </div>
          </div>



          <div className="col-6">
            <div className="input-group input-group-sm">
              <div className="input-group-prepend">
                <button className={[congthucStyle, "btn btn-switch"].join(' ')} disabled>Mn</button>
              </div>
              <select name="mon" id="mon" className="form-control">
                <option value="Ha">Ha</option>
                <option value="L">L</option>
                <option value="Ton">Ton</option>
              </select>
            </div>
          </div>

          {chuyende}

          <div className="col-6">
            <button className="btn btn-danger btn-sm w-100">Enter</button>
          </div>

          <div className="col-6">
            <button className={[congthucStyle, "btn btn-sm btn-switch w-100"].join(' ')} onClick={this.switchCongThucHandler}>
              <i className="fa fa-refresh"></i> {this.state.congthuc}
            </button>
          </div>


        </div>


      </form>
    );
  }
}

export default GhiNhoContent;



