import React, { Component } from 'react'
import Aux from '../../../hoc/_Aux/_Aux';

export default class Data extends Component {
  render() {
    return (
      <Aux>
        <div className="pointer" onClick={this.props.clicked}>Data 1</div>
        <div className="pointer" onClick={this.props.clicked}>Data 2</div>
        <div className="pointer" onClick={this.props.clicked}>Data 3</div>
      </Aux>
    )
  }
}



